const scrollToTopBtn = document.querySelector('#scrollToTopBtn');
const scrollingNavTrigger = 100;

scrollToTopBtn.addEventListener('click', () => {
	document.body.scrollTop = 0; // For Safari
	document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
});

window.addEventListener('scroll', () => {
	if (window.pageYOffset > scrollingNavTrigger) {
		scrollToTopBtn.classList.add('opacity1');
		scrollToTopBtn.style.cursor = 'pointer';
		scrollToTopBtn.classList.remove('opacity0');
	} else {
		scrollToTopBtn.classList.add('opacity0');
		scrollToTopBtn.style.cursor = 'auto';
		scrollToTopBtn.classList.remove('opacity1');
	}
});
